.about-container {
    text-align: center;
    margin: 20vh auto;
    padding: 20px;
    max-width: 70vw;
    background-color: #ecfcfe;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.about-heading {
    font-size: 2.5rem;
    color: #333;
    margin-bottom: 20px;
    animation: fadeInDown 1s ease;
}

.about-intro {
    font-size: 1.2rem;
    margin-bottom: 20px;
}

.about-highlight {
    font-size: 1.5rem;
    color: #007BFF;
    font-weight: bold;
    margin-bottom: 30px;
}

.about-btn {
    padding: 10px 20px;
    background-color: #007BFF;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1rem;
    transition: background-color 0.3s ease;
}

.about-btn:hover {
    background-color: #0056b3;
}

.about-details {
    margin-top: 20px;
    text-align: left;
    animation: fadeIn 1s ease;
}

.about-details ul {
    list-style-type: none;
    padding: 0;
}

.about-details ul li {
    font-size: 1.2rem;
    margin: 10px 0;
}

/* Animation */
@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes fadeInDown {
    0% {
        opacity: 0;
        transform: translateY(-20px);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}
